/* src/components/About.css */

.about {
    display: flex;
    align-items: center;
    gap: 2rem;
    padding: 2rem;
    background-color: #ffffff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
  }
  
  .profile-photo {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
    border: 4px solid #0077cc;
  }
  
  .about p {
    max-width: 600px;
    font-size: 1.1rem;
    line-height: 1.6;
  }
  