/* src/components/Skills.css */

.skills {
    text-align: center;
  }
  
  .skills h2 {
    margin-bottom: 2rem;
    font-size: 2rem;
    color: #0077cc;
  }
  
  .skills ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
    list-style-type: none;
  }
  
  .skills li {
    background-color: #0077cc;
    color: #ffffff;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    font-size: 1rem;
  }
  