/* src/components/Contact.css */

.contact {
    text-align: center;
  }
  
  .contact h2 {
    margin-bottom: 2rem;
    font-size: 2rem;
    color: #0077cc;
  }
  
  .contact form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    max-width: 500px;
    margin: 0 auto;
  }
  
  .contact input,
  .contact textarea {
    padding: 0.8rem;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .contact button {
    padding: 0.8rem;
    font-size: 1rem;
    background-color: #0077cc;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .contact button:hover {
    background-color: #005fa3;
  }
  