
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    background-color: #333;
    color: #ffffff;
  }
  
  .header h1 {
    font-size: 1.8rem;
    font-weight: bold;
  }
  
  .header nav ul {
    list-style-type: none;
    display: flex;
    gap: 1.5rem;
  }
  
  .header nav a {
    color: #ffffff;
    font-size: 1rem;
    text-transform: uppercase;
  }
  
  .header nav a:hover {
    color: #0077cc;
  }