/* src/components/Projects.css */

.projects {
    text-align: center;
  }
  
  .projects h2 {
    margin-bottom: 2rem;
    font-size: 2rem;
    color: #0077cc;
  }
  
  .project-cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.5rem;
  }
  
  .project-card {
    padding: 1.5rem;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
  }
  
  .project-card:hover {
    transform: scale(1.05);
  }
  
  .project-card h3 {
    font-size: 1.4rem;
    color: #333;
  }
  
  .project-card p {
    color: #666;
  }
  